import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import ReactAudioPlayer from 'react-audio-player';

import estampillaTitle from '../../../assets/estampilla-1.jpg';
import gif1 from '../../../assets/IMG_2687.gif';
import gif2 from '../../../assets/IMG_2215.gif';
import gif3 from '../../../assets/IMG_1733.gif';
import audio from '../../../assets/WhatsApp Audio 2022-03-24 at 11.34.07 PM.ogg';

export const NosotrosScreen = () => {
  return (
    <>
      <Container fluid="md">
        <Row>
          <Col>
            <Card>
              <Card.Body>
                <Card.Title style={{ color: "#A4133C" }}><b>LO NUESTRO</b> <img src={estampillaTitle} style={{ width: "35px", paddingBottom: "0.28rem", transform: [{ rotate: '45deg' }] }} /></Card.Title>
                <hr></hr>
                <Card.Text>
                  <p className='fs-6'>Nunca imaginé que lo de <b>nosotros</b> se volviese tan especial y hermoso. En este corto tiempo hemos pasado por cosas hermosas, hemos discutido, hemos solucionado y sacado lo nuestro adelante.</p>
                  <p className='fs-6'>Mi amor, tu y yo sabemos que lo nuestro es amor verdadero, donde ambos hemos aprendido amar y aceptar tanto nuestros defectos como virtudes y lo sorprenderte es que desde el primer día tuvimos esa conexión tan ESPECIAL que pocos comprenden y otros desean.</p>
                  <p className='fs-6'>Me terminaste de enamorar desde el primer día, mi amor. Siempre te lo recuerdo pero tu escencia y tu forma de expresarte me cautivaron y flecahron de una forma inimaginable.
                    La forma en la que me miraste desde el primer momento, la forma en como me besaste desde el primer momento, la forma en como me acariciaste y aunque esa fuese la primera vez me atraparon para toda la vida.</p>
                  <p className='fs-6'>En nuestro trayecto hemos pasado por discusiones, amores, momentos felices, momentos tristes y más. Pero, esos momentos así sean tristes o felices SIEMPRE querré que séan contigo. </p>
                  <p className='fs-6'>Como tu pareja, tu futuro prometido, tu amante, y tu futuro esposo yo siempre te voy amar, a querer, a valorar, a respetar, a honrar, a darte un gran lugar en mi vida y en mi ser.
                    Mi amor tu me has enseñado a lo que es sentir y saber AMAR a otra persona, y este AMOR que tengo solo te lo brindaré a ti.</p>
                  <p className='fs-6'>Solo quiero, deseo, y lucho que lo nuestro nunca acabe y sea para <b>SIEMPRE</b>.
                    Mi meta en esta relación es poder crecer y vernos evolucionar el uno al otro y te juro, que más de lo que lo haces, brillarás de una manera fantástica</p>
                  <p className='fs-6'>¡Gracias por estar en mi vida mi amor! <b>TE AMO MI CHIQUITA ❤️</b> </p>
                  <Row>
                    <ReactAudioPlayer
                      src={audio}
                      controls
                    />
                  </Row>
                  <br></br>
                  <Row>
                    <Col>
                      <img src={gif1} style={{ width: "100%" }} />
                    </Col>
                    <Col>
                      <img src={gif2} style={{ width: "100%" }} />
                    </Col>
                    <Col>
                      <img src={gif3} style={{ width: "100%" }} />
                    </Col>
                  </Row>
                  {/* <video  width="800" height="600" name="Video Name">
                    <source src={video}/>
                  </video> */}
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  )
}
